import React from "react"
import '../scss/custom.scss'

function getLetters(input) {
    input = String(input)
    var alphabet = 'abcdefghij'
    var result = ''
    var letter;
    for (letter of input) {
        result += alphabet[parseInt(letter)]
    }
    return result
}

function BlogArchiveYear(props){
    var year = "#" + getLetters(props.year)
    return <div className="card-header">
        <a className="collapsed card-link" data-toggle="collapse" href={year}>
            {props.year}
        </a>
    </div>
}

export default BlogArchiveYear;