import React from "react"
import '../scss/custom.scss'
import { Link } from 'gatsby'

function getLetters(input) {
    input = String(input)
    var alphabet = 'abcdefghij'
    var result = ''
    var letter;
    for (letter of input) {
        result += alphabet[parseInt(letter)]
    }
    return result
}

export default (props) => {
var year = new Date(props.data.frontmatter.date)
var thisYear = new Date()
var active = ''
if (year.getFullYear() === thisYear.getFullYear()) {
    active = 'show'
}
year = getLetters(year.getFullYear())
return <Link className="text-decoration-none" to={props.data.frontmatter.path} >
    <div id={year} className={"collapse " + active} data-parent="#accordion">
        <div className="card-body pb-0 pt-1">
            {props.data.frontmatter.title}<br />
            <small className='text-muted'>{props.data.frontmatter.date}</small>
            <hr className="mb-0 mt-1"/>
            {/* <div className="small muted mb-0" dangerouslySetInnerHTML={{ __html: props.data.excerpt }}/> */}
        </div>
    </div>
</Link>}